.about-us {
  width: 1024px;
  margin: 42px auto 0 auto;
}
.about-us > .ant-row {
  padding: 20px 30px;
}
.about-us-intro .about-us-title {
  padding: 10px 0;
  font-size: 24px;
  color: #000;
  border-bottom: 1px solid #f2f2f2;
}
.about-us-intro .about-us-info {
  padding: 10px 0;
  font-size: 14px;
  color: #666;
}
.about-contact {
  width: 1024px;
  margin: 0 auto;
}
.about-contact-form {
  padding-left: 30px;
}
.about-contact-form-item {
  margin-top: 8px;
}
.about-contact-form-item label {
  display: inline-block;
  width: 80px;
  text-align: left;
  margin-right: 16px;
  color: rgba(0, 0, 0, 0.85);
}
.about-contact-wx {
  margin-bottom: 24px;
}
.about-contact-wx h3 {
  font-size: 14px;
  color: #000;
  margin-bottom: 8px;
}
.about-contact-wx-img {
  width: 200px;
}
.about-contact-wx-img img {
  width: 100%;
}
